<template>
  <div id='translateForm' class='row'>
    <div class='col-md-4 col-md-offset-4' id="container">
      <form @submit='formSubmit' class='well form-inline' id='transForm'>
        <input type='text' placeholder='请输入需要翻译的内容' v-model='textToTranslate' class='form-control col-md-8' @keyup.enter="formSubmit">
        <select v-model='language' class='form-control col-md-2'>
          <option value='en'>English</option>
          <option value='zh'>中文</option>
          <option value='ru'>русский</option>
          <option value='ko'>한국어</option>
          <option value='ja'>日本語</option>
        </select>
        <input type='submit' value='翻译' class='btn btn-primary col-md-2'>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "translateForm",
  data: function() {
    return {
      textToTranslate: "",
      language: ""
    };
  },
  methods: {
    formSubmit: function(e) {
      this.$emit("submit", this.textToTranslate, this.language);
      e.preventDefault();
    }
  },
  created:  function(){
    this.language = "en"
  }
};
</script>

<style scoped>
#transForm {
  border-radius: 10px;
}
#translateForm {
  margin: 10px auto;
}
#container{
  width: 60vw;
  margin: 20px auto;
}
</style>



