<template>
  <div id='app'>
    <h1>在线翻译</h1>
    <h5 class='text-muted'>简单 / 易用 / 便捷</h5>
    <translateForm @submit='translateText'></translateForm>
    <translateOutput v-text='translatedText'></translateOutput>
  </div>
</template>

<script>
import TranslateForm from "./components/translateForm";
import TranslateOutput from "./components/translateOutput";
export default {
  name: "App",
  data: function() {
    return {
      translatedText: ""
    };
  },
  components: {
    TranslateForm,
    TranslateOutput
  },
  methods: {
    translateText: function(txt, lang) {
      this.$http
        .get(
          "https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180503T031454Z.c7ea5d39d3cabdfe.4d8717944bf04aaa3f878032b9c8ff2005bc2a98&lang=" +
            lang +
            "&text=" +
            txt
        )
        .then(response => {
          this.translatedText = response.body.text[0];
        });
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



