<template>
  <div id='translateOutput'>
    <h2>{{translatedText}}</h2>
  </div>
</template>

<script>
export default {
  name: "translateOutput",
  props: ["translatedText"]
};
</script>

<style scoped>
  h2{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 800;
    color: aqua;
  }
</style>


